import React, { Component } from 'react';
import styled from 'styled-components';
import Head from 'next/head'
import Link from 'next/link'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import Nav from '../components/Nav'

const PageWrapper = styled.div`
  width: 100%;
  max-width: 100vw;
  height: 100%;
  min-height: 100vh;
  box-sizing: border-box;
  padding-top: 96px;
`;

const PageTitle = styled.h1`
  font-size: 5rem;
  font-weight: bold;
  color: #000;
  position: relative;
  text-align: center;
  display: flex;
  width: calc(100% - 32px);
  margin: auto;
  margin-bottom: 24px;

  &::after {
    content: "";
    width: 100%;
    height: 8px;
    position: absolute;
    bottom: -8px;
    background: #000;
    left: 0;
    right: 0;
  }

  @media all and (max-width: 880px) {
    font-size: 3.5rem;
  }

  @media all and (max-width: 600px) {
    font-size: 2.5rem;
  }
`;


class Contact extends Component {

  render() {
    return (
      <PageWrapper className="contact">
        <Nav page="/contact" theme="black"/>
        <PageTitle className="contact__pageTitle">
          Contact
        </PageTitle>
        <Tabs>
          <TabList>
            <Tab>
              Beer
            </Tab>
            <Tab>
              Coffee
            </Tab>
            <Tab>
              In Person
            </Tab>
            <Tab>
              Phone
            </Tab>
            <Tab>
              Email
            </Tab>
          </TabList>

          <TabPanel>
            Beer
          </TabPanel>
          <TabPanel>
            Coffee
          </TabPanel>
          <TabPanel>
            In Person
          </TabPanel>
          <TabPanel>
            Phone
          </TabPanel>
          <TabPanel>
            Email
          </TabPanel>
        </Tabs>
      </PageWrapper>
    );
  }

}

export default Contact;



